import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { UserProvider } from '../../providers/user/user';
import { QuoteProvider } from "../../providers/quote/quote";

/**
 * Generated class for the MapPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-map',
  templateUrl: 'map.html',
})
export class MapPage {

  skill1:string;
  skill2:string;
  skill3:string;
  obstacle1:string;
  obstacle2:string;
  obstacle3:string;
  exposure1:string;
  exposure2:string;
  exposure3:string;
  behaviour1:string;
  behaviour2:string;
  behaviour3:string;
  vision:string;
  goal:string;

  constructor(public navCtrl: NavController, public navParams: NavParams, public userservice: UserProvider, public quote:QuoteProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MapPage');
    this.load();
    this.loaddetails();
    this.loaddets();
    this.loaduserdetails();
    this.loadquote();
    this.loadvision();
    this.loadgoal();
    
  }

  loaddetails() {
    this.userservice.obstacledetails().then((res: any) => {
     this.obstacle1=res.obstacle1;
     this.obstacle2=res.obstacle2;
      this.obstacle3=res.obstacle3;
    
    })
  }

  loaduserdetails() {
    this.userservice.skilldetails().then((res: any) => {
     this.skill1=res.skill1;
     this.skill2=res.skill2;
      this.skill3=res.skill3;
    
    })
  }
  load() {
    this.userservice.exposuredetails().then((res: any) => {
     this.exposure1=res.exposure1;
     this.exposure2=res.exposure2;
      this.exposure3=res.exposure3;
    
    })
  }

  loaddets(){
    this.userservice.behaviourdetails().then((res: any) => {
      this.behaviour1=res.behaviour1;
      this.behaviour2=res.behaviour2;
       this.behaviour3=res.behaviour3;

  })
}

loadquote(){
  this.quote.presentToast();
}


loadvision(){
  this.userservice.visiondetails().then((res: any) => {
    this.vision=res.vision;
})
}


loadgoal(){
  this.userservice.goaldetails().then((res: any) => {
    this.goal=res.goal;
})
}

}



